.section {
  z-index: 10;
  min-height: 100vh;
}

.calculator__container {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 320px;

  @media screen and (min-width: 450px) {
    width: 450px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    flex-direction: row;
  }
}

.calendar {
  padding-top: 80px;
  padding-bottom: 60px;
  width: 100%;

  @media screen and (min-width: 768px) {
    padding-top: 180px;
    padding-left: 32px;
    padding-bottom: 42px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 293px;
    padding-right: 124px;
    padding-left: 16px;
  }
}

.buttonPlus {
  display: block;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;
  border: none;
  border-radius: 50%;
  background-image: url(../../images/addBtn.svg);
  background-position: center;
  background-repeat: no-repeat;
  height: 48px;
  width: 48px;
  background-color: #fc842d;
  box-shadow: 0px 4px 10px rgba(252, 132, 45, 0.5);
  cursor: pointer;

  @media screen and (min-width: 768px) {
    display: none;
  }
}

.mobileModal {
  display: block;
}
